import React, { Component } from 'react';
import './App.css';
import { render } from 'react-dom'
import { Map, Marker, Popup, TileLayer } from 'react-leaflet'
import 'leaflet/dist/leaflet.css'
import L from 'leaflet'
require('./animatedMarker.js')
var tedIcon = L.icon({
                iconUrl: 'data:image/png;base64,',
                iconSize: [90, 90], // size of the icon
                popupAnchor: [0,-15]
                });
// var teddyRoute = [[48.440200, -123.360000],[48.435334, -123.358017],[48.432323, -123.358454],[48.432619, -123.361917],[48.432107, -123.361657],[48.432200, -123.365000]]
//
// var line = L.polyline(teddyRoute)
// var animatedMarker = L.animatedMarker(line.getLatLngs(),{
//   icon: tedIcon
// });
class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      date: new Date(),
      position: [
        48.432,
        -123.365
      ]
    };
  }

  componentDidMount() {
    this.timerID = setInterval(
      () => this.tick(),
      1000
    );
  }

  componentWillUnmount() {
    clearInterval(this.timerID);
  }

  tick() {
    this.setState({
      position: [
        48.432 + (Math.random() - 0.5) * .01,
        -123.365 + (Math.random() - 0.5) * .01
      ]
    });
  }

  render() {
    return (
      <div className="App">
        <Map center={[
        48.432,
        -123.365
      ]} zoom={13}>
          <TileLayer
            url="https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png"
            attribution="&copy; <a href=&quot;http://osm.org/copyright&quot;>OpenStreetMap</a> contributors"
          />
          <Marker position={this.state.position} icon={tedIcon}>
            <Popup>{this.state.date.toLocaleTimeString()}</Popup>
          </Marker>
        </Map>
      </div>
    );
  }
}

export default App;
